.todo-item {
  position: relative;
  font-size: 24px;
  border-bottom: 1px solid #ededed;
  &:last-child {
    border-bottom: none;
  }
  &__edit {
    display: none;
    position: relative;
    margin: 0;
    width: 100%;
    line-height: 1.4em;
    padding: 6px;
    border: 1px solid #999;
    box-shadow: inset 0 -1px 5px 0 color(#000 a(0.2));
    box-sizing: border-box;
    outline: none;
    font-size: 24px;
    font-family: inherit;
    font-weight: inherit;
    font-smoothing: antialiased;
    color: inherit;
  }
  &-editing {
    border-bottom: none;
    padding: 0;
    .todo-item__edit {
      display: block;
      width: 506px;
      padding: 13px 17px 12px 17px;
      margin: 0 0 0 43px;
    }
    .todo-item__content {
      display: none;
    }
    &:last-child {
      margin-bottom: -1px;
    }
  }
  &__toggle {
    text-align: center;
    width: 40px;
    height: auto;
    position: absolute;
    top: 0;
    bottom: 0;
    margin: auto 0;
    border: none; /* Mobile Safari */
    appearance: none;
    &:after {
      width: 40px;
      height: 40px;
      content: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" width="40" height="40" viewBox="-10 -18 100 135"><circle cx="50" cy="50" r="50" fill="none" stroke="#ededed" stroke-width="3"/></svg>');
    }
    &:checked:after {
      content: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" width="40" height="40" viewBox="-10 -18 100 135"><circle cx="50" cy="50" r="50" fill="none" stroke="#bddad5" stroke-width="3"/><path fill="#5dc2af" d="M72 25L42 71 27 56l-4 4 20 20 34-52z"/></svg>');
    }
  }
  label {
    white-space: pre-line;
    word-break: break-all;
    padding: 15px 60px 15px 15px;
    margin-left: 45px;
    display: block;
    line-height: 1.2;
    transition: color 0.4s;
  }
  &-completed label {
    color: #d9d9d9;
    text-decoration: line-through;
  }
  &__destroy {
    display: none;
    position: absolute;
    top: 0;
    right: 10px;
    bottom: 0;
    width: 40px;
    height: 40px;
    margin: auto 0;
    font-size: 30px;
    color: #cc9a9a;
    margin-bottom: 11px;
    transition: color 0.2s ease-out;
    &:hover {
      color: #af5b5e;
    }
    &:after {
      content: '×';
    }
  }
  &:hover .todo-item__destroy {
    display: block;
  }
}


@media screen and (-webkit-min-device-pixel-ratio:0) {
  .todo-item__toggle {
    background: none;
    height: 40px;
  }
}


